import { fireStore } from 'fbase';
// import { v4 as uuidv4 } from 'uuid';

const addLandingMessage = async (landingMessage) => {
  try {
    let messageList =
      (await fireStore.collection('landing').doc('message').get()).data() ?? [];
    Array.from(messageList).push(landingMessage);
    await fireStore.collection('landing').doc('messages').set(messageList);
    return null;
  } catch (error) {
    return error;
  }
};

const getPermission = async (permissionCode) => {
  const trimCode = permissionCode.replace(/(\s*)/g, '');
  const state =
    (await fireStore.collection('permission').doc('code').get()).data()
      .permissionCode === trimCode;
  if (state) {
    localStorage.setItem('isPermission', state);
  } else {
    localStorage.removeItem('isPermission');
  }
  return state;
};

export { getPermission, addLandingMessage };
